
.game {
  display: flex;
  flex-direction: row;
  text-align: center;
  height: 340px;
  width: 500px;
  position: relative;
  .gameBoard {
    width: 210px;
      .boardRow {
        &:after {
          clear: both;
          content: "";
          display: table;
        }
        .square {
          color: white;
          background: #fff;
          border: 1px solid #999;
          float: left;
          font-size: 24px;
          font-weight: bold;
          line-height: 34px;
          height: 60px;
          margin-right: -1px;
          margin-top: -1px;
          padding: 0;
          text-align: center;
          width: 60px;
          transition: 1s;
          box-shadow: 1px 0px 11px 2px #9af1d7;
          /* dark mode */
          background-color: rgb(24, 26, 27);
          border-top-color: rgb(71, 71, 71);
          border-right-color: rgb(71, 71, 71);
          border-bottom-color: rgb(71, 71, 71);
          border-left-color: rgb(71, 71, 71);
          box-shadow: rgb(14, 98, 73) 1px 0px 11px 2px;
          &:hover {
            background: #b1d0ee;
            border: 3px solid rgb(0, 189, 110);
            /* dark mode */
            background-color: rgb(46, 49, 51);
            border-top-color: rgb(0, 217, 126);
            border-right-color: rgb(0, 217, 126);
            border-bottom-color: rgb(0, 217, 126);
            border-left-color: rgb(0, 217, 126);
          }
          &:focus {
            outline: none;
          }
          &.squareW {
            background: dodgerblue;
            font-size: 28px;
            color: antiquewhite;
            &:hover {
              background: dodgerblue;
            }
          }
          &.squareA {
            margin-right: 4px;
            margin-top: 4px;
          }
        }
      }
  }
  .gameInfo {
    width: 150px;
    .status {
      color: white;
      font-size: 14px;
      transition: 0.3s;
    }
    .winner {
      color: #5da1e4;
      font-size: 20px;
    }
    ol {
      padding-left: 30px;
      list-style: none;
      li {
        button {
          &.travel {
          background: rgb(99, 167, 217);
          color: white;
          padding: 4px;
          margin-top: 2px;
          border-radius: 6px;
          border: none;
          transition: 0.3s;
          cursor: pointer;
          &:active {
            background: rgb(15, 98, 146);
          }
        }
         &.current {
           padding: 8px;
           background: rgb(91, 180, 176);
         }
        }
      }
    }
  }
  .totalScore {
    display: grid;
    grid-template-columns: 70px 60px;
    grid-template-rows: 40px 40px;
    span {
      font-size: 23px;
      color: #90e3f9;
      margin: 10px;
      &.header {
        font-weight: bolds;
      }
      &.xi, &.omikron {
        font-size: 18px;
      }
    }
  }
  .timer {
    position: absolute;
    right: 97px;
    top: 135px;
    color: white;
    font-size: 25px;
    transition: right 0.5s ease;
    &.green {
      color: green;
    }
    &.yellow {
      color: yellow;
    }
    &.orange {
      color: orange;
    }
    &.red {
      color: red;
    }
    &.big {
      font-size: 30px;
    }
    &.oIsNext {
      right: 33px;
    }
  }
}

.scoreRotation {
  animation: spinHorizontal 2s normal forwards ease;
}

@keyframes spinHorizontal {
  0% {
    transform: rotateY(0deg);
    font-size: 18px;
    color: #90e3f9;
    font-weight: normal;
  }
  80% {
    transform: rotateY(360deg);
    font-size: 30px;
    color: #5bb4b0;
    font-weight: 500;
  }
  100% {
    transform: rotateY(0deg);
    font-size: 18px;
    color: #90e3f9;
    font-weight: normal;
  }
}
.newGameButton {
  position: absolute;
  top: 21px;
  left: 14px;
  background: white;
  width: 40px;
  height: 40px;
  transform: rotateZ(0deg);
  transition: transform 0.5s ease;
  border-radius: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 40px;
  cursor: pointer;
  $plusWidth: 65%;
  &::before {
    content: '';
    position: absolute;
    display: block;
    width: $plusWidth;
    height: 3px;
    top: 50%;
    transform: translate3d(-50%, -50%, 0px);
    left: 50%;
    background: #2807a9;
  }
  &::after {
    content: '';
    position: absolute;
    display: block;
    width: $plusWidth;
    height: 3px;
    top: 50%;
    transform: translate3d(-50%, -50%, 0px) rotateZ(90deg);
    transition: transform 0.5s ease;
    left: 50%;
    background: #01b582;
  }
  &.on {
    transform: rotateZ(180deg);
    &::after {
    transform: translate3d(-50%, -50%, 0px) rotateZ(0deg);
    }
  }
}
.gamesMenu {
  display: flex;
  z-index: 1;
  justify-content: center;
  flex-wrap: wrap;
  width: 160px;
  background: white;
  position: absolute;
  transform: rotateY(0);
  top: 20px;
  left: 58px;
  border-radius: 9px;
  transition: top 500ms ease, transform 500ms ease;
  &.menuHidden {
    top:-170px;
    transform: rotateY(90deg);
  }
  input {
    width: 100%;
    border: none;
    height: 30px;
    border-radius: 30px;
    background:#5bb4b0;
    margin: 4px;
    text-align: center;
    font-size: 17px;
    &:focus, &:active {
      border: none;
    }
    &.submit {
      background: #2807a9;
      color: white;
      width: 50%;
      border-radius: 6px;
      &:hover {
        cursor: pointer;
        background: lighten($color: #2807a9, $amount: 15)
      }
    }
  }
}
